.calendar {
    overflow:auto;
    margin: auto;
    align-items: center;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-style: double;
    background-color: rgb(222, 246, 255);
    border-color: #000 ;
    border-width: thick;

}

.calendar-header {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px;
    margin-bottom: 20px;
    font-size: large;
}

.calendar-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 10px;
    width: 100%;
    max-width: 1200px;

}

.calendar-day {
    padding: 10px;
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 5px;
    min-height: 100px;
    position: relative;
}

.date-number {
    font-size: 1.5em;
    font-weight: bold;
    color: #000;
}

.calendar-day .events {
    margin-top: 15px;
}

.calendar-day .event {
    padding: 5px;
    margin-top: 5px;
    border-radius: 3px;
    font-size: 0.85em;
    text-align: center;
}

.calendar-day.empty {
    background-color: white;
    border: 1px solid #ddd;;


}

.event.tourney{
    background-color: #fae3fd;
    border: 1px solid #f990d8;
}

.event.practice {
    background-color: #67d8fe;
    border: 1px solid #05c0ff;
}

.event.depart {
    background-color: rgb(255, 241, 114);
    border: 1px solid rgb(255, 208, 0);
}

.event.other {
    background-color: rgb(157, 255, 159);
    border: 1px solid rgb(9, 255, 14);
}

.event.lobster {
    background-color: rgb(252, 183, 146);
    border: 1px solid rgb(255, 153, 0);
}
button {
    font-weight: bold;
    width: 75px;
    background-color: white;
    border-radius: 25%;
}


